header {
  height: 100px;
  background-color: rgba(0, 120, 200, .7);
}

body {
  margin: 0 auto;
}

.main-div {
  max-width: 1200px;
  margin: 20px auto;
}

canvas {
  width: 1200px;
}

.flatpickr-days:not(.flatpickr-day) {
  background-color: #89b4f9;
}

.flatpickr-day {
  opacity: 1;
}

.flatpickr-day:nth-child(7n+1), .flatpickr-day:nth-child(7n+7) {
  color: red;
}

.flatpickr-calendar .flatpickr-day.prevMonthDay {
  color: rgba(57, 57, 57, .3);
}

.flatpickr-day.flatpickr-disabled, .flatpickr-day.nextMonthDay {
  color: rgba(57, 57, 57, .1);
}

.tickers-div {
  display: flex;
}

@media screen and (max-width: 420px) {
  .main-div {
    max-width: 350px;
    margin: auto 20px;
  }

  .tickers-div {
    max-width: 350px;
    flex-direction: column;
    align-items: center;
    padding-bottom: 15px;
  }

  .select1 {
    width: 320px;
    margin-bottom: 15px;
    font-size: 20px;
  }

  .select2 {
    width: 320px;
    font-size: 20px;
  }

  .buttons-div {
    max-width: 350px;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    display: flex;
  }
}

